  <template>
    <div>
      这是标题管理
      <table class="userList" border="0" cellspacing="0">
            <tr>
                <th>用户名</th>
                <th>姓名</th>
                <th>年龄</th>
                <th>性别</th>
                <th>地址</th>
                <th>联系方式</th>
            </tr>
            <tr v-for="(item,index) in items" keys="index">
                <td>{{item.name}}</td>
                <td>{{item.userName}}</td>
                <td>{{item.age}}</td>
                <td>{{item.sex}}</td>
                <td>{{item.address}}</td>
                <td>{{item.phone}}</td>
            </tr>
        </table>

    </div>
  </template>

<script>

export default {
  data(){
    return {
        items:[],
    }
  },
  methods:{
    query(){
        this.axios.searchData({}).then(res => {
        if (res.returnCode === "000000") {
          this.items = res.data.doc;
          console.log(this.items);
        }
      });

    },
  },
  mounted (){
    var vm = this;
    vm.query();
  }
}
</script>



